						axisTick: {
							inside: false,
							length: 0,
							lineStyle: {

							}
							// show:false
						},
					},
					// {type:'category',position:'bottom',name:'LA',axisPointer:{type:'none'}},
					// {type:'category',position:'top',name:'LA',boundaryGap:false,offset:30}
				],
				yAxis: [{
					type: 'value',
					gridIndex: 0
				}, ],
				dataset: {
					source: [
						// ["15:26:02", 3, null],
						// ["15:25:59", 3, null],
						// ["15:25:57", 3, null],
						// ["15:25:54", 3, null],
